import * as THREE from 'three'
import gsap from 'gsap'
import vertexShader from './shaders/vertexShader.glsl'
import fragmentShader from './shaders/fragShader.glsl'

export default class Figure {
    constructor(scene, offset, image, hoverImage) {
        // this.$image = document.querySelector(".tile__image")
        this.scene = scene
        this.loader = new THREE.TextureLoader()

        // this.image = this.loader.load(this.$image.src)
        // this.hoverImage = this.loader.load(document.querySelector(".tile__image_hover").src, (texture) => {
        //     // console.info(texture.image.naturalWidth)
        //     this.naturalWidth = texture.image.naturalWidth
        //     this.naturalHeight = texture.image.naturalHeight
        //     // this.sizes.set(window.innerHeight * (this.naturalWidth / this.naturalHeight), window.innerHeight)
        //     this.sizes.set(window.innerWidth , window.innerHeight * (this.naturalHeight / this.naturalWidth ))
        //     this.mesh.scale.set(this.sizes.width, this.sizes.height, 1)
        // })
        this.image = this.loader.load(image.src)
        this.hoverImage = this.loader.load(hoverImage.src, (texture) => {
            this.naturalWidth = texture.image.naturalWidth
            this.naturalHeight = texture.image.naturalHeight
            this.sizes.set(window.innerHeight * (this.naturalWidth / this.naturalHeight), window.innerHeight)
            this.mesh.scale.set(this.sizes.width, this.sizes.height, 1)
        })

        this.sizes = new THREE.Vector2(0, 0)
        this.offset = new THREE.Vector2(0, 0)
        this.mouse = new THREE.Vector2(0, 0)

        // this.getSizes()
        this.createMesh(offset)

        window.addEventListener('mousemove', (event) => {
            this.onMouseMove(event)
        })

        window.addEventListener('resize', () => {
            this.material.uniforms.u_res.value = new THREE.Vector2(window.innerWidth, window.innerHeight)
        })
    }

    getSizes() {
        const { width, height, left, top } = this.$image.getBoundingClientRect()
        
        console.info(width, height, left, top)
        // this.sizes.set(window.innerWidth, window.innerHeight)
        // this.sizes.set(window.innerHeight * (this.naturalWidth / this.naturalHeight), window.innerHeight)
        this.offset.set(left - window.innerWidth / 2 + this.sizes.width / 2, + top + window.innerHeight / 2 - this.sizes.height / 2 - top * 2)
    }

    createMesh(offset) {
        this.uniforms = {
            u_image: { type: 't', value: this.image },
            u_hoverimage: { type: 't', value: this.hoverImage },
            u_time: { value: 0 },
            u_mouse: { value: this.mouse },
            u_res: { value: new THREE.Vector2(window.innerWidth, window.innerHeight) }
        }

        this.geometry = new THREE.PlaneBufferGeometry(1, 1, 1, 1)
        this.material = new THREE.ShaderMaterial({
            uniforms: this.uniforms,
            fragmentShader: fragmentShader,
            vertexShader: vertexShader,
            defines: {
                PR: window.devicePixelRatio.toFixed(1)
            }
        })

        this.mesh = new THREE.Mesh(this.geometry, this.material)
        // this.mesh.position.set(this.offset.x, this.offset.y, 0)
        this.mesh.position.set(offset.x, offset.y, offset.z)
        this.mesh.scale.set(this.sizes.width, this.sizes.height, 1)

        this.scene.add(this.mesh)
    }

    onMouseMove(event) {
        gsap.to(
            this.mouse, .5, {
            x: (event.clientX / window.innerWidth) * 2 - 1,
            y: -(event.clientY / window.innerHeight) * 2 + 1,
        })

        gsap.to(
            this.mesh.rotation, .5,
            {
                x: this.mouse.y * .3,
                y: -this.mouse.x * (Math.PI / 6)
            })
    }

    update() {
        this.uniforms.u_time.value += .01
    }
}
