import Figure from "./figure"
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import image from '../static/image.jpg'
import hoverImage from '../static/52c9998932fa2ee30b3b9184_rw_1920.jpg'


const perspective = 800
export default class Scene {
    constructor() {
        this.container = document.getElementById('stage')

        this.scene = new THREE.Scene()
        this.renderer = new THREE.WebGLRenderer({
            // alpha: true,
            canvas: this.container,
        })

        this.sizes = {
            width: window.innerWidth,
            height: window.innerHeight
        }
        this.renderer.setSize(this.sizes.width, this.sizes.height)
        this.renderer.setPixelRatio(window.devicePixelRatio)
        this.renderer.clearColor('#000000')

        this.initSizeListener()
        this.initLights()
        this.initCamera()
        // this.initControl()

        this.image = new Image()
        this.image.src = image

        this.hoverImage = new Image()
        this.hoverImage.src = hoverImage
        // this.hoverImage.addEventListener('load', () => {
        //     console.info("2nd img loaded")
        // })

        this.figure = new Figure(this.scene, new THREE.Vector3(0, 0, 0), this.image, this.hoverImage)
        // this.figure2 = new Figure(this.scene, new THREE.Vector3(window.innerWidth / 2 - 650, 0, 0))
        
        this.update()
    }

    initLights() {
        const ambientLight = new THREE.AmbientLight('#ffffff', 2)
        this.scene.add(ambientLight)
    }

    initCamera() {
        const fov = (180 * (2 * Math.atan(window.innerHeight / 2 / perspective))) / Math.PI

        this.camera = new THREE.PerspectiveCamera(fov, window.innerWidth / window.innerHeight, 2, 10000)
        this.camera.position.set(0, 0, perspective)
    }

    initControl(){
        this.control = new OrbitControls(this.camera, this.container)
        this.control.enableDamping = true
    }

    update() {
        requestAnimationFrame(this.update.bind(this))

        // this.control.update()

        this.renderer.render(this.scene, this.camera)

        this.figure.update()
        // this.figure2.update()

    }


    initSizeListener(){
        window.addEventListener('resize', () => {
            this.sizes.width = window.innerWidth
            this.sizes.height = window.innerHeight

            this.camera.aspect = this.sizes.width / this.sizes.height
            this.camera.updateProjectionMatrix()

            this.renderer.setPixelRatio(window.devicePixelRatio)
            this.renderer.setSize(this.sizes.width, this.sizes.height)
        })
    }

}